import React, { Component } from 'react';
import {Section, SectionTitle} from './Section';
import HobbiesIcon from 'material-ui/svg-icons/action/favorite';

class Hobbies extends Component {
  render() {
    return (
      <Section>
        <SectionTitle title="Centres d'intérêt" rightIcon={<HobbiesIcon/>}/>
      </Section>
    );
  }
}

export default Hobbies;



